<script>

import axios from "axios"
import vueCookies from 'vue-cookies'
import markdownit from 'markdown-it'
import {validateSession} from '@/store/session'

import avatar1 from '@/assets/images/avatars/1.jpg'
import avatar2 from '@/assets/images/avatars/2.jpg'

export default {
  name: "SARAS",
  
  data: () => {
    return {
      // headerToken
      APIHOST: process.env.VUE_APP_APIS_HOST === undefined ? '' : process.env.VUE_APP_APIS_HOST,
      headers: {
        'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json; charset=utf-8',
        'Authorization': vueCookies.get('accessToken')
      },

      //Validation
      validatedCustom01: null,

      // Markdown
      md: markdownit(),

      // Avatars
      app_avatar: avatar1,
      user_avatar: avatar2,

      // Data
      userDetails: vueCookies.get('userDetails'),
      chatHistory: [],
      userMessage: null,

      //Typing
      showTyping: false,
    }
  },
  methods: {

    checkFormValidity(event) {

      const form = event.currentTarget
      if (form.checkValidity() === false) {
        event.preventDefault()
        event.stopPropagation()
      } else {
        this.sendChat()
      }
      this.validatedCustom01 = true
    },

    getRecentChats() {
      let chatDetailsUrl = this.APIHOST + '/mainapp/getChatDump?simplified=1'
      let config = {
        method: 'get',
        maxBodyLength: Infinity,
        url: chatDetailsUrl,
        headers: this.headers,
      };

      axios.request(config)
      .then((response) => {
        console.log(response)

        this.chatHistory = []

        for (let i = 0; i < response.data.chats.length; i++) {
          
          let chat = response.data.chats[i]

          let dt_userTime = new Date(chat.userTime)          
          let dth_userTime = dt_userTime.getFullYear() + "/" + dt_userTime.getMonth() + "/" + dt_userTime.getDate() + " - " + dt_userTime.getHours() + ":" + dt_userTime.getMinutes()

          let dt_botTime = new Date(chat.botTime)  
          let dth_botTime = dt_botTime.getFullYear() + "/" + dt_botTime.getMonth() + "/" + dt_botTime.getDate() + " - " + dt_botTime.getHours() + ":" + dt_botTime.getMinutes()

          let chatFormatted = {
            "ask": {
              "text": chat.user,
              "time": dth_userTime
            },
            "response": {
              "text": atob(chat.bot),
              "time": dth_botTime
            }
          }
          this.chatHistory.push(chatFormatted)
        }
        console.log(this.chatHistory)
      })
      .catch((error) => {
        console.log(error);
      });
    },

    sendChat() {
      // alert(this.userMessage)

      let message = this.userMessage
      
      let data = JSON.stringify({
        "message": message,
        "datetime": new Date().toISOString().slice(0,-5)
      })

      let config = {
        method: 'post',
        maxBodyLength: Infinity,
        url: this.APIHOST + '/mainapp/webBotIncomingMessageFromUser',
        headers: this.headers,
        data : data
      };

      axios.request(config)
        .then((response) => {

          // enable receiving
          this.showTyping = true
          this.userMessage = null
          this.chatHistory.push({
            "ask": {
              "text": message,
            },
          })

          setTimeout(this.getRecentChats(), 7000);

          // console.log(JSON.stringify(response.data));
          // alert(JSON.stringify(response.data))
      })
      .catch((error) => {
        console.log(error);
      });

    },

  }, //end of methods

  beforeCreate() {
    validateSession(this.$router)
  }, // end beforeCreate

  created() {
    this.getRecentChats()
  }, // end created

}

</script>

